<template>
  <div
    class="page-login"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-login__root">
      <div id="page-login__wrapp">
        <the-header-new />

        <div class="page-login__main">
          <div class="page-login__main-content">
            <the-my-wallet :tokenAddress="tokenAddress" />
          </div>
        </div>
        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Ð¡Ñ‚Ñ€Ð°Ð½Ð¸Ñ†Ð° Ð³ÐµÐ½ÐµÑ€Ð°Ñ†Ð¸Ð¸ ÐºÐ¾ÑˆÐµÐ»ÑŒÐºÐ°
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';
import store from '@/store';
import { ETH_ADDRESS } from '@/util/constants';

// Components
import TheMyWallet from '@/containers/blocks/TheMyWallet';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';


export default {
  name: 'page-aboutus',

  props: {
    tokenAddress: {
      type: String,
      required: true,
    },
  },

  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async beforeRouteEnter(to, from, next) {
    await store.dispatch('tokens/loadTokens');
    if (to.params.walletToken) {
      next();
    } else {
      const owned = store.getters['balance/ethAndOwnedTokens'];
      const activeContract = store.getters['tokens/activeTokenContract'];
      const walletToken = owned.some(t => t.contract === activeContract)
        ? activeContract
        : ETH_ADDRESS;
      next({
        name: to.name,
        params: { ...to.params, walletToken },
      });
    }
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheMyWallet,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-login {
  $this: &;

  .the-header-new {
    background-color: #0f0d44;
  }

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #f6f7fb;
    margin-top: -120px;
    padding: 120px 30px 0;
    box-sizing: border-box;
    z-index: 0;
  }

  &__main-content {
    width: 100%;
    max-width: 1200px;
    padding: 70px 0 0;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(1279) {
      padding-top: 30px;
    }
  }
}

</style>



