import { toDisplayedPrivateKey } from '@/util/crypto';

export default ({ address, privateKey }) => {
  const formattedPrivateKey = toDisplayedPrivateKey(privateKey);
  const keyFileBlob = new Blob(
    [`Your Wallet: ${address}\r\nPrivate key: ${formattedPrivateKey}`],
    { type: 'text/plain' },
  );
  this.keyFileUrl = URL.createObjectURL(keyFileBlob);
  const link = document.createElement('a');
  link.download = `${address}.txt`;
  link.href = this.keyFileUrl;
  link.click();
  URL.revokeObjectURL(this.keyFileUrl);
};



