<script>
import EBalanceTransferOperations from '@/components/EBalanceTransferOperations';
/**
 * Component: ÐšÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚ Ð¼Ð¾ÐµÐ³Ð¾ ÐºÐ¾ÑˆÐµÐ»ÑŒÐºÐ°
 *
 * @version 1.0.1
 */
import { mapGetters } from 'vuex';
import { routesMixin, searchTokenMixin } from '@/util/mixins';
import { DEPOSIT_OPERATION, ETH_BASE_ADDRESS } from '@/util/constants';
import { NEWS_AND_ORDERS_TAB_ACTIVE } from '@/store/constants';
// import store from '@/store';
import { toDisplayedPrivateKey } from '@/util/crypto';
import downloadWallet from '@/util/downloadWallet';

import TheMyWalletTransactions from '@/containers/blocks/TheMyWalletTransactions';
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import EHeading from '@/components/EHeading';
import EButton from '@/components/EButton';
import ECopyText from '@/components/ECopyText';
import ETokenIcon from '@/components/ETokenIcon';

import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconLedger from '@/icons/e-icon_nano-ledger-s.svg';
import iconImportant from '@/icons/e-icon__important.svg';
import iconArrow from '@/icons/e-icon_arrow.svg';
import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';
import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';
import iconTransferAvia from '@/icons/e-icon_transfer-avia.svg';
import iconWithdrawToWallet from '@/icons/e-icon_withdraw-to-wallet.svg';
import iconClose from '@/icons/e-icon_close.svg';
import iconSendToContract from '@/icons/e-icon_send-to-contract.svg';
import iconTrade from '@/icons/e-icon_trade.svg';
import iconSearch from '@/icons/e-icon_search.svg';
import iconWallet from '@/icons/e-icon__wallet.svg';
import iconRefresh from '@/icons/e-icon__refresh.svg';

/**
 * Assuming that user has active wallet selected
 */
export default {
  name: 'the-my-wallet',

  props: {
    tokenAddress: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      isSelectTokenDropdown: false,
      tokenSearch: '',
      isPrivateBalanceRefreshed: false,

      // Icons
      iconMetamask,
      iconLedger,
      iconImportant,
      iconArrow,
      iconArrowDowner,
      iconDottedAnimation,
      iconTransferAvia,
      iconWithdrawToWallet,
      iconClose,
      iconSendToContract,
      iconTrade,
      iconSearch,
      iconWallet,
      iconRefresh,
    };
  },

  computed: {

    isEth() {
      return this.tokenAddress === ETH_BASE_ADDRESS;
    },

    /**
     * Token balance
     * @returns {{privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber}}
     */
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](this.tokenAddress);
    },

    tradeRoute() {
      return { name: 'futures' };
    },

    chartsRoute() {
      const parentRoute = this.$route.matched[0];
      const chartsInBackground = parentRoute != null && parentRoute.name === 'token';
      return chartsInBackground
        ? this.$closedPopupRoute
        : { name: 'tokens' };
    },

    token() {
      return this.$store.getters['tokens/tokenByContract'](this.tokenAddress);
    },

    filteredCurrencies() {
      const tokens = this.tokenSearch !== '' ? this.$searchTokens(this.tokenSearch, this.$store.getters['tokens/ethAndTokens']) : this.$store.getters['balance/ethAndOwnedTokens'];
      return tokens;
    },

    displayedPrivateKey() {
      return toDisplayedPrivateKey(this.activeWallet.privateKey);
    },

    depositOperations() {
      return this.balance.operations.filter(o => o.type === DEPOSIT_OPERATION);
    },

    nonDepositOperations() {
      return this.balance.operations.filter(o => o.type !== DEPOSIT_OPERATION);
    },

    isPrivateBalanceFetching() {
      return this.$store.getters['balance/isPrivateBalanceRefreshing'] || this.isPrivateBalanceRefreshed;
    },

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      isLogin: 'wallets/isLogin',
      contractBalanceLoading: 'balance/isContractBalanceRefreshing',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    async isLogin(isLogin) {
      if (!isLogin) {
        this.$router.push(this.$closedPopupRoute);
      }
    },
  },

  // async beforeRouteEnter(to, from, next) {
  //   await store.dispatch('tokens/loadTokens');
  //   if (to.params.walletToken) {
  //     next();
  //   } else {
  //     const owned = store.getters['balance/ethAndOwnedTokens'];
  //     const activeContract = store.getters['tokens/activeTokenContract'];
  //     const walletToken = owned.some(t => t.contract === activeContract)
  //       ? activeContract
  //       : ETH_ADDRESS;
  //     next({
  //       name: to.name,
  //       params: { ...to.params, walletToken },
  //     });
  //   }
  // },

  created() {
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
  },


  beforeDestroy() {
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
  },

  methods: {
    async refreshPrivateBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.isPrivateBalanceRefreshed = true;

      let balance;
      try {
        balance = await (
          this.isEth
            ? balanceApi.getPrivateEth()
            : balanceApi.getPrivateToken({ tokenAddress: this.tokenAddress })
        );
      } catch (e) {
        const walletName = this.$store.getters['wallets/walletBalanceName'];
        const currency = this.$store.getters['tokens/getTokenByName'](this.tokenAddress);
        this.$store.commit('ui/ADD_NOTIFICATION', {
          status: 'warning',
          headline: 'Refresh failure',
          message: `${walletName} ${currency} balance refresh failure`,
        }, { root: true });
      }

      if (balance) {
        this.$store.commit('balance/UPDATE_PRIVATE_BALANCE', { eth: balance });
      }
      this.isPrivateBalanceRefreshed = false;
    },

    download() {
      downloadWallet(this.activeWallet);
    },

    startDeposit() {
      this.$store.dispatch('deposit/startDeposit', this.tokenAddress);
    },

    startWithdrawal() {
      this.$store.dispatch('deposit/startWithdrawal', this.tokenAddress);
    },

    startTransfer() {
      this.$store.dispatch('deposit/startTransfer', this.tokenAddress);
    },

    goToCancelOrder() {
      const route = { name: 'token', params: { token: this.tokenAddress } };
      this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', NEWS_AND_ORDERS_TAB_ACTIVE);
      this.$router.push(route);
    },

    selectTokenShowDropdown() {
      this.isSelectTokenDropdown = true;
      this.$nextTick(() => {
        const tokenSearchInput = this.$refs.tokenSearchInput;
        if (tokenSearchInput !== undefined) {
          tokenSearchInput.focus();
        }
      });
    },

    selectTokenHideDropdown() {
      this.isSelectTokenDropdown = false;
      this.tokenSearch = '';
    },

    setToken(address) {
      this.$router.push({
        name: this.$route.name,
        params: {
          ...this.$route.params,
          walletToken: address,
        },
      });
      this.selectTokenHideDropdown();
    },
  },

  components: {
    EBalanceTransferOperations,
    TheMyWalletTransactions,
    EBigNumber,
    EIcon,
    EHeading,
    EButton,
    ECopyText,
    ETokenIcon,
  },

  mixins: [routesMixin, searchTokenMixin],
};
</script>

