    display: flex;
    flex-flow: column;
    align-items: flex-end;

    @include r(767) {
      align-items: normal;
      flex: 0 0 100%;
      margin-top: 12px;
    }
  }

  &__balance-item-inner {
    flex: 0 0 auto;
  }

  &__balance-amount-switch-wrap {
    flex: 1;
    display: flex;
    padding-top: 18px;
    align-items: flex-start;
    justify-content: center;
  }

  &__balance-icon {
    width: 16px;
    height: 16px;
    margin-right: 9px;
  }

  &__balance-label {
    color: #000;
    font-size: 13px;
    font-weight: 500;
    display: flex;
    align-items: center;
    vertical-align: middle;
    line-height: 1.24;
    padding-left: 36px;
    box-sizing: border-box;
  }

  &__pending.e-balance-transfer-operations {
    padding-top: 3px;
    font-size: 13px;
  }

  &__options {
    padding-top: 8px;
  }

  &__options-item {
    cursor: pointer;
    display: flex;
    padding: 8px 0;
    box-sizing: border-box;
    height: 44px;
    align-items: center;
    color: #0123E7;
    text-decoration: none;
    font-size: 10px;
    font-weight: 500;
    text-transform: uppercase;
    transition: color .15s ease-in-out;

    &:hover {
      color: #0D105B;
    }

    &.cancel {
      color: #DA4830;

      #{$this}__options-icon {
        width: 13px;
        height: 15px;
        margin-right: 10px;
      }

      svg {
        fill: #E8A095;
        transition: fill .15s ease-in-out;
      }

      &:hover {
        svg {
          fill: #DA4830;
        }
      }
    }
  }

  &__options-icon {
    width: 18px;
    margin-right: 14px;

    .e-icon svg {
      position: relative;
    }
  }

  &__btn-charts {
    margin-top: 5px;

    @include r(640) {
      margin-top: 12px;
    }
  }

  /* SELECT */
  &__select-wrapper {
    width: 409px;
  }

  &__select {
    position: relative;
    width: 100%;
    height: 60px;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    border-radius: 5px;
    background-color: #fff;
  }

  &__select-result {
    cursor: pointer;
    position: relative;

    .e-icon {
      position: absolute;
      right: 8px;
      top: calc(50% - 2px);
      width: 10px;
      height: 10px;
      margin-right: 11px;

      svg {
        fill: $color-blue;
      }
    }
  }

  &__select-result-content {
    display: flex;
    padding-right: 40px;
    height: 60px;
    box-sizing: border-box;
  }

  &__select-result-token-icon {
    display: flex;
    flex: 0 0 60px;
    align-items: center;
    justify-content: center;

    .e-tocken-icon {
      width: 30px;
      height: 30px;
    }
  }

  &__select-result-token-short {
    font-size: 16px;
    line-height: 12px;
    color: #09168A;
    font-weight: 500;
  }

  &__select-result-token-balance {
    font-size: 14px;
    color: #0D105B;
  }

  &__select-result-token-fullname {
    color: #8384AB;
    font-size: 14px;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__select-result-token {
    flex: 1;
    display: flex;
    align-items: center;
  }

  &__select-result-token-amount {
    flex: 1;
  }

  &__select-result-token-fullname {
    flex: 0 0 70px;
    text-align: right;
  }

  &__select-dropdown {
    position: absolute;
    width: 100%;
    bottom: 58px;
    transform: translate(0, 100%);
    z-index: 3;
    background-color: #fff;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    padding-top: 5px;
    border-radius: 0 0 5px 5px;
  }

  &__select-dropdown-content {
    width: 100%;
    height: 200px;
    overflow-y: auto;
    margin: 3px 0;
    position: relative;

    &::-webkit-scrollbar {
      width: 2px;
      height: 0;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__select-dropdown-content-empty {
    font-size: 16px;
    color: rgba(131, 132, 171, .4);
    text-transform: uppercase;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__select-item {
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 60px;
    border-top: 1px solid #F3F6FF;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: #F3F6FF;
    }
  }

  &__select-dropdown-token-icon {
    display: flex;
    flex: 0 0 60px;
    align-items: center;
    justify-content: center;

    .e-tocken-icon {
      width: 30px;
      height: 30px;
    }
  }

  &__select-dropdown-token-fullname {
    font-size: 16px;
    line-height: 12px;
    color: #09168A;
    font-weight: 500;
  }

  &__select-dropdown-search {
    display: flex;
    align-items: center;
    padding: 0 18px 0 20px;

    .e-icon {
      width: 20px;
      height: 20px;

      svg {
        fill: #0123e7;
      }
    }
  }

  &__select-dropdown-search-input {
    flex: 1;
    height: 50px;
    margin-left: 18px;
    font-size: 18px;
    font-weight: 500;

    &::placeholder {
      color: rgba(131, 132, 171, 0.4);
    }
  }


  &__balance-amount-switch {
    display: flex;
    width: 87px;
    height: 41px;
    transform: translateY(-50%);
    box-shadow: 0 3px 10px 0 rgba(148, 165, 180, .14);
    border-radius: 40px;
  }

  &__balance-amount-switch-button {
    cursor: pointer;
    background: #fff;
    transition: all .15s ease-in-out;
    flex: 0 0 44px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: #0223E7;
      border-color: transparent;

      #{$this}__balance-amount-switch-button-icon svg path {
        fill: #fff;
      }
    }

    &.is-active {
      background: #0F28B8;
      border-color: transparent;

      #{$this}__balance-amount-switch-button-icon svg path {
        fill: #fff;
      }
    }

    &:first-child {
      border-radius: 40px 0 0 40px;

      #{$this}__balance-amount-switch-button-icon {
        transform: rotate(90deg) translate(0, -2px);
      }
    }

    &:last-child {
      border-radius: 0 40px 40px 0;
      border-left: 1px solid #DBE1E5;

      #{$this}__balance-amount-switch-button-icon {
        transform: rotate(-90deg) translate(0, -2px);
      }
    }
  }

  &__balance-amount-switch-button-icon {
    width: 12px;
    height: 15px;
    display: flex;
    align-items: center;

    svg path {
      fill: #0223E7;
      transition: fill .15s ease-in-out;
    }
  }

  &__dotted-animation {
    width: 48px;
    transform: translateY(50%);
    height: 24px;
    margin-left: 2px;
  }

  /* Balance amount */
  .balance-amount {
    display: flex;
    align-items: center;
    font-weight: 500;
    letter-spacing: 0.62px;
    font-size: 20px;
    line-height: 1.2;
    height: 24px;

    .e-big-number {
      padding-right: 3px;
    }
  }

  .balance-amount__main {
    color: #0D105B;
  }

  .balance-amount__pale {
    color: rgba(131, 132, 171, .4);
  }

  &__transactions {
    flex: 0 0 auto;
  }

  &__dark {
    .e-heading__title {
      color: #fff;
    }

    .e-heading__subtitle {
      color: $color-dark-grey;
    }

    #{$this}__select,
    #{$this}__select-dropdown,
    #{$this}__select-dropdown-search-input {
      background: #232323;
      color: $color-dark-grey;
    }

    #{$this}__select-item {
      border-color: #393939;

      #{$this}__select-dropdown-token-fullname {
        color: $color-dark-grey;
      }

      &:hover {
        background-color: #2b2b2b;

        #{$this}__select-dropdown-token-fullname {
          color: $color-dark-grey;
        }
      }
    }

    #{$this}__select-result-token-short {
      color: #fff;
    }

    #{$this}__select-result-token-balance,
    #{$this}__select-result-token-fullname {
      color: $color-dark-grey;
    }

    #{$this}__select-result .e-icon svg {
      fill: $color-dark-grey;
    }

    #{$this}__select-dropdown-content {
      &::-webkit-scrollbar-track {
        background-color: #212121;
      }

      &::-webkit-scrollbar-thumb {
        background-color: $color-dark-grey;
      }
    }

    .e-button_fill_green {
      color: #fff;
    }

    #{$this}__key-warn-message {
      color: #232323;
    }

    #{$this}__key-warn-icon {
      background: #232323;
    }

    #{$this}__addresses {
      background: #232323;
    }

    #{$this}__addresses-headline {
      color: #8D9098;
    }

    #{$this}__copy {
      .e-copy-text__title {
        color: #8D9098;
      }
    }

    .e-copy-text__qr .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-copy-text__qr-text,
    .e-copy-text__toggle-text,
    .e-copy-text__toggle:hover .e-copy-text__toggle-text {
      color: $color-blue-dark-mode;
    }

    .e-copy-text__qr-code-wrapper {
      &:hover {
        .e-copy-text__qr-icon .e-icon svg {
          fill: $color-blue-dark-mode;
        }

        .e-copy-text__qr-text {
          color: $color-blue-dark-mode;
        }
      }
    }

    #{$this}__select-dropdown-search .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-button_fill_blue-white {
      color: #232323;
      background: $color-blue-dark-mode;
      border-color: $color-blue-dark-mode;

      &:hover {
        background: $color-blue-dark-mode;
      }
    }

    .e-button_fill_white {
      background: #2E2E2E;
      color: $color-blue-dark-mode;
      box-shadow: none;

      &:before {
        box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.14);
      }
    }

    .balance-amount {
      color: #fff;
    }

    .balance-amount__pale {
      color: $color-dark-grey;
    }

    #{$this}__balance-refresh svg g {
      fill: $color-blue-dark-mode;
    }

    #{$this}__options-item {
      color: $color-blue-dark-mode;

      &:hover {
        color: #304cf4;
      }
    }
  }

  #{$this}__balance-label {
    color: $color-dark-grey;
  }

  #{$this}__iconSendToContract svg g {
    fill: $color-dark-grey;
  }

  #{$this}__iconTransferAvia svg path,
  #{$this}__iconTrade svg path {
    fill: $color-dark-grey;
  }

  #{$this}__iconWithdrawToWallet svg g path:nth-child(2) {
    fill: $color-dark-grey;
  }

  #{$this}__key-warn {
    background-color: $color-blue-dark-mode;
  }

  #{$this}__copy .e-copy-text__hash {
    color: $color-blue-dark-mode;
  }

  .e-copy-text_compact .e-copy-text__qr-text {
    color: $color-blue-dark-mode;
  }

  .the-my-wallet-transactions__transaction-main-description,
  .the-my-wallet-transactions__no-transaction,
  .the-my-wallet-transactions__transaction-date,
  .the-my-wallet-transactions__transaction-description,
  .the-my-wallet-transactions__transaction-amount,
  .the-my-wallet-transactions__token-symbol {
    color: $color-dark-grey;
  }

  .the-my-wallet-transactions__transaction-status.deactivated:before {
    border-color: $color-dark-grey;
  }
}
</style>



